import axios from "axios";
import React, { useEffect, useState } from "react";
import { IoMdAddCircle } from "react-icons/io";
import { courseStoreInterface } from "../../../../actions/course";
import { schoolLevelsStoreInterface } from "../../../../actions/school_levels";
import { PopupAlertData } from "../../../../components/Alert/Popup";
import { course, CourseChapter } from "../../../../interfaces/course";
import { formErrorInterface } from "../../../../interfaces/form";
import { PopupAlertTheme } from "../../../../interfaces/popupAlert";
import { API } from "../../../../utils/api";
import SchoolLevelSelect from "../SettingsComponents/SchoolLevelSelect";
import { CoursesAction, S_CourseDetails } from "./Courses";
import { v4 as uuidv4 } from "uuid";

interface AddCourseProps {
  closeModal: (state: boolean) => void;
  courses: courseStoreInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
  selectedCourse: S_CourseDetails | null;
  deleteSelected: (selectedDetail: S_CourseDetails) => void;
  schoolLevels: schoolLevelsStoreInterface;
  setSelectedCourse: (courseDetail: S_CourseDetails | null) => void;
  chaptersOnly: boolean;
}

const AddCourse = (props: AddCourseProps) => {
  // States
  const [course_id, setCourse_id] = useState<string>("");
  const [course_name, setCourse_name] = useState<string>("");
  const [course_code, setCourse_code] = useState<string>("");
  const [description, setDescription] = useState<string>("");
  const [chapters, setChapters] = useState<CourseChapter[]>([]);
  const [school_levels, setSchool_levels] = useState<string[]>([]);
  const [formError, setFormError] = useState<formErrorInterface | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [addChapter, setAddChapter] = useState<{
    openAdd: boolean;
    chapter: string;
  }>({ openAdd: false, chapter: "" });
  const submitCourse = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // validation
    if (
      props.selectedCourse?.action === CoursesAction.Update &&
      course_id === ""
    ) {
      return setFormError({
        element: "course_id",
        msg: "Please choose Course",
      });
    } else if (course_name === "") {
      return setFormError({
        element: "course_name",
        msg: "Please fill the course name",
      });
    } else if (course_code === "") {
      return setFormError({
        element: "course_code",
        msg: "Please fill the course code",
      });
    } else if (description === "") {
      return setFormError({
        element: "description",
        msg: "Please fill the description",
      });
    } else {
      setLoading(true);
      try {
        // Update current
        if (
          props.selectedCourse !== null &&
          props.selectedCourse.selectedCourse.course_id !== undefined
        ) {
          let obj: course = {
            course_id: course_id,
            course_name: course_name,
            course_code: course_code,
            description: description,
            school_levels: school_levels,
            chapters: chapters,
          };
          await axios.patch(`${API.APPLICATION}/course`, obj);
          props.PushPopupAlert({
            title: `Course updated successfully`,
            description: `After updating the course, you have access to delete and update it!`,
            theme: PopupAlertTheme.info,
          });
          props.setSelectedCourse({
            action: props.selectedCourse.action,
            selectedCourse: obj,
          });
          props.closeModal(false);
          // console.log("Result: ", res);
        } else {
          // Add new
          await axios.post(`${API.APPLICATION}/course`, {
            course_name: course_name,
            course_code: course_code,
            description: description,
            school_levels: school_levels,
            chapters: chapters,
          });
          props.PushPopupAlert({
            title: `Course added successfully`,
            description: `After adding the Course, you have access to delete and update it!`,
            theme: PopupAlertTheme.success,
          });
          props.closeModal(false);
          // console.log("Result: ", res);
        }
      } catch (error: any) {
        if (error.response) {
          if (error.response.data) {
            if (error.response.data.errors !== undefined) {
              if (error.response.data.errors.length > 0) {
                props.PushPopupAlert({
                  title: error.response.data.errors[0].message,
                  theme: PopupAlertTheme.danger,
                });
              } else {
                props.PushPopupAlert({
                  title: `Failed to add course! Try again`,
                  theme: PopupAlertTheme.danger,
                });
              }
            } else {
              props.PushPopupAlert({
                title: `Failed to add course! Try again`,
                theme: PopupAlertTheme.danger,
              });
            }
          } else {
            props.PushPopupAlert({
              title: `Failed to add course! Try again`,
              theme: PopupAlertTheme.danger,
            });
          }
        }
        console.log("Err: ", { ...error });
      }
      setLoading(false);
    }
  };

  const addNewChapter = () => {
    if (addChapter.chapter === "") {
      return setAddChapter({ openAdd: false, chapter: "" });
    }
    if (addChapter.openAdd === true) {
      setAddChapter({ openAdd: true, chapter: "" });
      setChapters([
        {
          name: addChapter.chapter,
          status: 1,
          id: uuidv4(),
        },
        ...chapters,
      ]);
    }
  };

  const removeChapter = (chapter: CourseChapter) => {
    setChapters(chapters.filter((itm) => itm.id !== chapter.id));
  };

  useEffect(() => {
    if (props.selectedCourse !== null) {
      // console.log("Sle: ", props.selectedCourse);
      if (course_id === "") {
        setCourse_id(props.selectedCourse.selectedCourse.course_id);
      }
      if (course_name === "") {
        setCourse_name(props.selectedCourse.selectedCourse.course_name);
      }
      if (course_code === "") {
        setCourse_code(props.selectedCourse.selectedCourse.course_code);
      }
      if (description === "") {
        setDescription(props.selectedCourse.selectedCourse.description);
      }
      if (school_levels.length === 0) {
        setSchool_levels(props.selectedCourse.selectedCourse.school_levels);
      }
      if (chapters.length === 0) {
        if (props.selectedCourse.selectedCourse.chapters !== undefined) {
          setChapters(props.selectedCourse.selectedCourse.chapters);
        }
      }
    }
  }, [
    props,
    course_id,
    course_name,
    course_code,
    description,
    school_levels,
    chapters,
  ]);
  return (
    <form onSubmit={submitCourse} className="-mt-2">
      <div className="grid md:grid-cols-12 gap-5">
        {props.chaptersOnly === false && (
          <div className="col-span-6">
            <SchoolLevelSelect
              schoolLevels={props.schoolLevels}
              school_levels={school_levels}
              setSchool_levels={setSchool_levels}
              loading={loading}
            />
          </div>
        )}
        <div
          className={`${
            props.chaptersOnly === false ? "col-span-6" : "col-span-12"
          }`}
        >
          {props.chaptersOnly === false && (
            <div className="flex flex-col mb-2">
              <span className="text-sm text-gray-600 font-bold mb-1">
                Course name
              </span>
              <input
                disabled={loading}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  setCourse_name(e.target.value);
                  setFormError(null);
                }}
                type="text"
                value={course_name}
                className={`input-white border-2 ${
                  formError !== null && formError.element === "course_name"
                    ? "border-red-500"
                    : "border-gray-500"
                }`}
              />
              {formError !== null && formError.element === "course_name" && (
                <span className="text-red-600 font-bold text-xs">
                  {formError.msg}
                </span>
              )}
            </div>
          )}
          {props.chaptersOnly === false && (
            <div className="flex flex-col mb-2">
              <span className="text-sm text-gray-600 font-bold mb-1">
                Course code
              </span>
              <input
                disabled={loading}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  setCourse_code(e.target.value);
                  setFormError(null);
                }}
                type="text"
                value={course_code}
                className={`input-white border-2 ${
                  formError !== null && formError.element === "course_code"
                    ? "border-red-500"
                    : "border-gray-500"
                }`}
              />
              {formError !== null && formError.element === "course_code" && (
                <span className="text-red-600 font-bold text-xs">
                  {formError.msg}
                </span>
              )}
            </div>
          )}
          {props.chaptersOnly === false && (
            <div className="flex flex-col mb-2">
              <span className="text-sm text-gray-600 font-bold mt-2 mb-1">
                Description
              </span>
              <textarea
                disabled={loading}
                onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
                  setDescription(e.target.value);
                  setFormError(null);
                }}
                value={description}
                className={`input-white border-2 ${
                  formError !== null && formError.element === "description"
                    ? "border-red-500"
                    : "border-gray-500"
                }`}
                style={{ minHeight: "100px" }}
              >
                {description}
              </textarea>
              {formError !== null && formError.element === "description" && (
                <span className="text-red-600 font-bold text-xs">
                  {formError.msg}
                </span>
              )}
            </div>
          )}
          <div className="flex flex-col mb-2">
            <div className="flex items-center justify-between">
              <span className="text-gray-600 font-bold">Course chapters</span>
              {addChapter.openAdd === false && (
                <div
                  onClick={() => setAddChapter({ openAdd: true, chapter: "" })}
                  className="cursor-pointer btn bg-blue-50 text-blue-600 text-sm flex flex-row gap-1 items-center"
                >
                  <IoMdAddCircle className="text-2xl" />
                  <span>Add chapter</span>
                </div>
              )}
            </div>
            {/* Add chapter */}
            <div>
              {addChapter.openAdd === true && (
                <div className="flex flex-row items-center justify-between gap-2 mt-2">
                  <input
                    onChange={(e) => {
                      setAddChapter({ openAdd: true, chapter: e.target.value });
                    }}
                    value={addChapter.chapter}
                    type="text"
                    placeholder="Chapter name"
                    className="input-white border-2 rounded border-gray-500"
                  />
                  <div
                    onClick={() => loading === false && addNewChapter()}
                    className={`${
                      loading === false
                        ? "cursor-pointer"
                        : "cursor-not-allowed"
                    } btn ${
                      addChapter.chapter === ""
                        ? loading === true
                          ? "bg-gray-200"
                          : "bg-red-600"
                        : "bg-blue-600"
                    } text-white text-sm flex flex-row gap-1 items-center`}
                  >
                    <span>{addChapter.chapter === "" ? "Close" : "Add"}</span>
                  </div>
                </div>
              )}
            </div>
            {chapters.length === 0 ? (
              <div className="rounded bg-gray-50 px-2 py-6 mt-2 flex flex-col text-sm text-gray-400 font-extrabold text-center">
                No chapters added
              </div>
            ) : (
              <div>
                {chapters.map((item, i) => (
                  <div
                    key={i + 1}
                    className={`flex flex-row items-center justify-between border ${
                      props.selectedCourse !== null &&
                      props.selectedCourse.selectedCourse.chapters &&
                      props.selectedCourse.selectedCourse.chapters.filter(
                        (itm) => itm.id === item.id
                      ).length === 0
                        ? "bg-gray-50 border-gray-200 text-gray-500"
                        : "bg-white border-blue-200 text-blue-600"
                    } my-1 pl-2 pr-1 py-1 rounded text-sm font-bold`}
                  >
                    <span>{item.name}</span>
                    {props.chaptersOnly === false && (
                      <div
                        onClick={() => loading === false && removeChapter(item)}
                        className={`btn ${
                          loading === false
                            ? "cursor-pointer"
                            : "cursor-not-allowed"
                        } bg-red-100 text-red-700 text-xs`}
                      >
                        Remove
                      </div>
                    )}
                  </div>
                ))}
              </div>
            )}
          </div>
          {addChapter.chapter === "" && (
            <div className="flex flex-row justify-between items-center">
              {props.selectedCourse?.action === CoursesAction.View ? (
                <button
                  onClick={() =>
                    props.selectedCourse &&
                    props.deleteSelected({
                      action: CoursesAction.Delete,
                      selectedCourse: props.selectedCourse.selectedCourse,
                    })
                  }
                  type="button"
                  className="btn border border-red-600 text-red-700"
                >
                  Delete
                </button>
              ) : (
                <div></div>
              )}
              <button
                disabled={loading}
                type="submit"
                className={`btn bg-blue-600 text-white ${
                  loading === true
                    ? "animate__animated animate__flash animate__infinite"
                    : ""
                }`}
              >
                {loading === true
                  ? "Please wait..."
                  : props.selectedCourse !== null
                  ? "Update course"
                  : "Add course"}
              </button>
            </div>
          )}
        </div>
      </div>
    </form>
  );
};

export default AddCourse;
