import React, { Fragment, useEffect, useState } from "react";
import { MdEdit } from "react-icons/md";
import { CgDetailsMore } from "react-icons/cg";
import { HiOutlineDocumentSearch } from "react-icons/hi";
import { connect } from "react-redux";
import {
  Auth,
  loadApplicationPeriods,
  setSelectedPeriod,
  applicationPeriodsStoreInterface,
  schoolLevelsStoreInterface,
  loadSchoolLevels,
  setSelectedEducationCategory,
  System,
  PushPopupAlert,
  courseStoreInterface,
  loadCourses,
  setSelectedCourse,
} from "../../../../actions";
import Loading from "../../../../components/Loading/Loading";
import { StoreState } from "../../../../reducers";
import Modal, { ModalSize, Themes } from "../../../../components/Modal/Modal";
import { PopupAlertData } from "../../../../components/Alert/Popup";
import { IoMdTrash } from "react-icons/io";
import axios from "axios";
import { API } from "../../../../utils/api";
import { PopupAlertTheme } from "../../../../interfaces/popupAlert";
import { course } from "../../../../interfaces";
import AddCourse from "./AddCourse";
import searchData from "../../../../utils/search";

interface CoursePropsInterface {
  system: System;
  course: courseStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
  loadSchoolLevels: (callback: (loading: boolean) => void) => void;
  loadCourses: (callback: (loading: boolean) => void) => void;
  setSelectedCourse: (course: course | null) => void;
}

export enum CoursesAction {
  Create = "Create",
  View = "View",
  Delete = "Delete",
  Update = "Update",
}

export interface S_CourseDetails {
  action: CoursesAction;
  selectedCourse: course;
}

const _Courses = (props: CoursePropsInterface) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [addNew, setAddNew] = useState<boolean>(false);
  const [search, setSearch] = useState<string>("");
  const [selectedCourse, setSelectedCourseComp] =
    useState<S_CourseDetails | null>(null);

  const closeAddNewModal = (state: boolean) => {
    props.loadCourses(setLoading);
    setAddNew(state);
  };

  const setSelectedCourse = (course_selected: S_CourseDetails | null) => {
    setSelectedCourseComp(course_selected);
    if (course_selected !== null) {
      props.setSelectedCourse(course_selected.selectedCourse);
    }
  };

  const deleteCourseFn = async (course: course) => {
    if (course.course_id !== undefined) {
      setLoading(true);
      try {
        await axios.delete(`${API.APPLICATION}/course/${course.course_id}`);
        props.loadCourses(setLoading);
        props.PushPopupAlert({
          title: `Course removed successfully`,
          theme: PopupAlertTheme.success,
        });
        setSelectedCourse(null);
        // console.log("Deleted: ", res);
      } catch (error) {
        props.PushPopupAlert({
          title: `Acton failed, Try again later!`,
          theme: PopupAlertTheme.warning,
        });
        setLoading(false);
        setSelectedCourse(null);
        console.log("err: ", error);
      }
      // setLoading(false);
    }
  };

  useEffect(() => {
    if (
      props.schoolLevels.school_levels === null ||
      props.schoolLevels.school_levels.length === 0
    ) {
      props.loadSchoolLevels(setLoading);
    }
    if (props.course.courses === null || props.course.courses.length === 0) {
      props.loadCourses(setLoading);
    }
  }, [props, setLoading]);
  return (
    <Fragment>
      <div className="mt-1">
        <div className="flex flex-row justify-between items-center bg-gray-100 py-1">
          <div className="text-blue-600 font-extrabold text-xl px-2">
            List of courses
          </div>
          {loading === false && (
            <div
              onClick={() => setAddNew(true)}
              className="btn bg-blue-600 text-white cursor-pointer hover:bg-blue-800"
            >
              Add new
            </div>
          )}
        </div>
        <div>
          <input
            onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
              setSearch(e.target.value)
            }
            type="text"
            className="input-white border border-gray-500 my-2"
            placeholder="Search"
            value={search}
          />
        </div>
        <div className="flex flex-col">
          {props.course.courses === null || loading === true ? (
            <Loading />
          ) : props.course.courses.length === 0 ? (
            <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
              <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
              <span>No courses available</span>
            </div>
          ) : (
            searchData(props.course.courses, search).map((item, i) => (
              <div
                className={`border flex flex-row px-3 py-2 rounded items-center my-1 justify-between`}
              >
                <div className="flex flex-col">
                  <span className="text-gray-600 font-extrabold">
                    {item.course_name}
                  </span>
                  <span className={`text-xs pr-5 text-gray-500`}>
                    {item.description}
                  </span>
                </div>
                <div className="flex flex-row items-center gap-2">
                  <div
                    onClick={() =>
                      setSelectedCourse({
                        action: CoursesAction.View,
                        selectedCourse: item,
                      })
                    }
                    className="bg-gray-50 hover:bg-gray-200 rounded-full px-1 py-1 cursor-pointer"
                  >
                    <CgDetailsMore className="text-3xl text-gray-700" />
                  </div>
                  <div
                    onClick={() =>
                      setSelectedCourse({
                        action: CoursesAction.Update,
                        selectedCourse: item,
                      })
                    }
                    className="bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 cursor-pointer"
                  >
                    <MdEdit className="text-3xl text-blue-700" />
                  </div>
                  <div
                    onClick={() =>
                      setSelectedCourse({
                        action: CoursesAction.Delete,
                        selectedCourse: item,
                      })
                    }
                    className="bg-gray-100 hover:bg-red-100 rounded-full px-1 py-1 cursor-pointer"
                  >
                    <IoMdTrash className="text-3xl text-yellow-700" />
                  </div>
                </div>
              </div>
            ))
          )}
        </div>
      </div>
      {(addNew === true ||
        (selectedCourse !== null &&
          (selectedCourse.action === CoursesAction.Update ||
            selectedCourse.action === CoursesAction.View))) && (
        <Modal
          title={
            <div className="flex flex-row gap-4 items-center">
              <div className="flex flex-col">
                <span className="font-extrabold text-2xl text-gray-500">
                  {selectedCourse !== null &&
                  selectedCourse.action === CoursesAction.Update
                    ? selectedCourse.selectedCourse.course_name
                    : selectedCourse !== null &&
                      selectedCourse.action === CoursesAction.View
                    ? selectedCourse.selectedCourse.course_name
                    : "Add new course"}
                </span>
                <span className="text-sm text-gray-500 font-normal">
                  {selectedCourse !== null &&
                  selectedCourse.action === CoursesAction.Update
                    ? "Update the selected course"
                    : selectedCourse !== null &&
                      selectedCourse.action === CoursesAction.View
                    ? "View more details for the selected course"
                    : "Course will be used in different components in the system, make sure what you're creating"}
                </span>
              </div>
            </div>
          }
          backDrop={true}
          theme={Themes.default}
          close={() => {
            setAddNew(false);
            setSelectedCourse(null);
          }}
          widthSizeClass={ModalSize.extraLarge}
          displayClose={true}
          backDropClose={true}
          padding={{ title: true, body: true }}
        >
          <AddCourse
            closeModal={closeAddNewModal}
            courses={props.course}
            PushPopupAlert={props.PushPopupAlert}
            selectedCourse={selectedCourse}
            deleteSelected={setSelectedCourse}
            schoolLevels={props.schoolLevels}
            setSelectedCourse={setSelectedCourse}
            chaptersOnly={false}
          />
        </Modal>
      )}
      {selectedCourse !== null &&
        selectedCourse.action === CoursesAction.Delete && (
          <Modal
            title={
              <div className="flex flex-row gap-4 items-center">
                <div className="flex flex-col">
                  <span className="font-extrabold text-2xl text-gray-500">
                    {selectedCourse.selectedCourse.course_name}
                  </span>
                </div>
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() => setSelectedCourse(null)}
            widthSizeClass={ModalSize.medium}
            displayClose={true}
            backDropClose={true}
            padding={{ title: true, body: true }}
          >
            <div className="-mt-6">
              <span className="text-red-600 font-bold">
                Are you sure do you want to delete this course?
              </span>
              <div className="flex flex-row items-center justify-between mt-2">
                <div
                  onClick={() => setSelectedCourse(null)}
                  className="btn bg-gray-100 text-gray-700 cursor-pointer hover:bg-gray-200"
                >
                  Cancel
                </div>
                <div
                  onClick={() => deleteCourseFn(selectedCourse.selectedCourse)}
                  className="btn bg-red-600 text-white cursor-pointer hover:bg-red-800"
                >
                  Yes, Delete
                </div>
              </div>
            </div>
          </Modal>
        )}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  periods,
  schoolLevels,
  system,
  course,
}: StoreState): {
  auth: Auth;
  periods: applicationPeriodsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  system: System;
  course: courseStoreInterface;
} => {
  return {
    auth: auth,
    periods: periods,
    schoolLevels: schoolLevels,
    system: system,
    course: course,
  };
};

const Courses = connect(mapStateToProps, {
  loadApplicationPeriods,
  setSelectedPeriod,
  loadSchoolLevels,
  setSelectedEducationCategory,
  PushPopupAlert,
  loadCourses,
  setSelectedCourse,
})(_Courses);

export default Courses;
