import axios from "axios";
import React, { useEffect, useState } from "react";
import { schoolLevelsStoreInterface } from "../../../../actions/school_levels";
import { PopupAlertData } from "../../../../components/Alert/Popup";
import { formErrorInterface } from "../../../../interfaces/form";
import { PopupAlertTheme } from "../../../../interfaces/popupAlert";
import { API } from "../../../../utils/api";
import SchoolLevelSelect from "../SettingsComponents/SchoolLevelSelect";
import {
  EducationCategoryTypeAction,
  E_EducationCategoryDetails,
} from "./EducationCategories";

interface AddEducationCategoryProps {
  closeModal: (state: boolean) => void;
  education_categories: schoolLevelsStoreInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
  selectedEducationCategory: E_EducationCategoryDetails | null;
  deleteSelected: (selectedDetail: E_EducationCategoryDetails) => void;
  schoolLevels: schoolLevelsStoreInterface;
}

const AddEducationCategory = (props: AddEducationCategoryProps) => {
  // States
  const [education_institute_id, setEducation_institute_id] =
    useState<string>("");
  const [education_category_title, setEducation_category_title] =
    useState<string>("");
  const [description, setDescription] = useState<string>("");
  const [school_levels, setSchool_levels] = useState<string[]>([]);
  const [formError, setFormError] = useState<formErrorInterface | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const submitEducationCategory = async (
    e: React.FormEvent<HTMLFormElement>
  ) => {
    e.preventDefault();
    // validation
    if (
      props.selectedEducationCategory?.action ===
        EducationCategoryTypeAction.Update &&
      education_institute_id === ""
    ) {
      return setFormError({
        element: "education_institute_id",
        msg: "Please choose education category",
      });
    } else if (education_category_title === "") {
      return setFormError({
        element: "education_category_title",
        msg: "Please fill the education category title",
      });
    } else if (description === "") {
      return setFormError({
        element: "description",
        msg: "Please fill the description",
      });
    } else {
      setLoading(true);
      try {
        // Update current
        console.log("Sel schoo le: ", school_levels);
        if (
          props.selectedEducationCategory !== null &&
          props.selectedEducationCategory.selectedEducationCategory
            .education_institute_id !== undefined
        ) {
          await axios.patch(`${API.APPLICATION}/educationinstitute`, {
            education_institute_id: education_institute_id,
            education_category_title: education_category_title,
            description: description,
            school_levels: school_levels,
          });
          props.PushPopupAlert({
            title: `Education category updated successfully`,
            description: `After updating the education category, you have access to delete and update it!`,
            theme: PopupAlertTheme.info,
          });
          props.closeModal(false);
          // console.log("Result: ", res);
        } else {
          // Add new
          await axios.post(`${API.APPLICATION}/educationinstitute`, {
            education_category_title: education_category_title,
            description: description,
            school_levels: school_levels,
          });
          props.PushPopupAlert({
            title: `Education category added successfully`,
            description: `After adding the Education category, you have access to delete and update it!`,
            theme: PopupAlertTheme.success,
          });
          props.closeModal(false);
          // console.log("Result: ", res);
        }
      } catch (error) {
        props.PushPopupAlert({
          title: `Failed to add Education category! Try again`,
          theme: PopupAlertTheme.danger,
        });
        console.log("Err: ", error);
      }
      setLoading(false);
    }
  };

  useEffect(() => {
    if (props.selectedEducationCategory !== null) {
      if (education_institute_id === "") {
        setEducation_institute_id(
          props.selectedEducationCategory.selectedEducationCategory
            .education_institute_id
        );
      }
      if (education_category_title === "") {
        setEducation_category_title(
          props.selectedEducationCategory.selectedEducationCategory
            .education_category_title
        );
      }
      if (description === "") {
        setDescription(
          props.selectedEducationCategory.selectedEducationCategory.description
        );
      }
      if (school_levels.length === 0) {
        setSchool_levels(
          props.selectedEducationCategory.selectedEducationCategory
            .school_levels
        );
      }
    }
  }, [
    props,
    education_institute_id,
    education_category_title,
    description,
    school_levels,
  ]);
  return (
    <form onSubmit={submitEducationCategory} className="-mt-2">
      <div className="grid md:grid-cols-12 gap-5">
        <div className="col-span-6">
          <SchoolLevelSelect
            schoolLevels={props.schoolLevels}
            school_levels={school_levels}
            setSchool_levels={setSchool_levels}
            loading={loading}
          />
        </div>
        <div className="col-span-6">
          <div className="flex flex-col mb-2">
            <span className="text-sm text-gray-600 font-bold mb-1">Title</span>
            <input
              disabled={loading}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                setEducation_category_title(e.target.value);
                setFormError(null);
              }}
              type="text"
              value={education_category_title}
              className={`input-white border-2 ${
                formError !== null && formError.element === "title"
                  ? "border-red-500"
                  : "border-gray-500"
              }`}
            />
            {formError !== null && formError.element === "title" && (
              <span className="text-red-600 font-bold text-xs">
                {formError.msg}
              </span>
            )}
          </div>
          <div className="flex flex-col mb-2">
            <span className="text-sm text-gray-600 font-bold mt-2 mb-1">
              Description
            </span>
            <textarea
              disabled={loading}
              onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
                setDescription(e.target.value);
                setFormError(null);
              }}
              value={description}
              className={`input-white border-2 ${
                formError !== null && formError.element === "description"
                  ? "border-red-500"
                  : "border-gray-500"
              }`}
              style={{ minHeight: "100px" }}
            >
              {description}
            </textarea>
            {formError !== null && formError.element === "description" && (
              <span className="text-red-600 font-bold text-xs">
                {formError.msg}
              </span>
            )}
          </div>
          <div className="flex flex-row justify-between items-center">
            {props.selectedEducationCategory?.action ===
            EducationCategoryTypeAction.View ? (
              <button
                onClick={() =>
                  props.selectedEducationCategory &&
                  props.deleteSelected({
                    action: EducationCategoryTypeAction.Delete,
                    selectedEducationCategory:
                      props.selectedEducationCategory.selectedEducationCategory,
                  })
                }
                type="button"
                className="btn border border-red-600 text-red-700"
              >
                Delete
              </button>
            ) : (
              <div></div>
            )}
            <button
              disabled={loading}
              type="submit"
              className={`btn bg-blue-600 text-white ${
                loading === true
                  ? "animate__animated animate__flash animate__infinite"
                  : ""
              }`}
            >
              {loading === true
                ? "Please wait..."
                : props.selectedEducationCategory !== null
                ? "Update"
                : "Add"}
            </button>
          </div>
        </div>
      </div>
    </form>
  );
};

export default AddEducationCategory;
