import React, { Fragment, useEffect, useState } from "react";
import { MdEdit } from "react-icons/md";
import { CgDetailsMore } from "react-icons/cg";
import { HiOutlineDocumentSearch } from "react-icons/hi";
import { connect } from "react-redux";
import {
  Auth,
  loadApplicationPeriods,
  setSelectedPeriod,
  applicationPeriodsStoreInterface,
  schoolLevelsStoreInterface,
  loadEducationCategories,
  setSelectedEducationCategory,
  questionTypesStoreInterface,
  loadQuestionTypes,
  System,
  PushPopupAlert,
} from "../../../../actions";
import Loading from "../../../../components/Loading/Loading";
import { StoreState } from "../../../../reducers";
import Modal, { ModalSize, Themes } from "../../../../components/Modal/Modal";
import AddQuestionType from "./AddQuestionType";
import { PopupAlertData } from "../../../../components/Alert/Popup";
import { IoMdTrash } from "react-icons/io";
import { questionType } from "../../../../interfaces/question";
import axios from "axios";
import { API } from "../../../../utils/api";
import { PopupAlertTheme } from "../../../../interfaces/popupAlert";

interface QuestionTypesPropsInterface {
  system: System;
  questionTypes: questionTypesStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

export enum QT_QuestionTypeAction {
  Create = "Create",
  View = "View",
  Delete = "Delete",
  Update = "Update",
}

export interface QT_QuestionTypeDetails {
  action: QT_QuestionTypeAction;
  selectedQuestionType: questionType;
}

const _QuestionTypes = (props: QuestionTypesPropsInterface) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [addNew, setAddNew] = useState<boolean>(false);
  const [selectedQuestionType, setSelectedQuestionType] =
    useState<QT_QuestionTypeDetails | null>(null);

  const closeAddNewModal = (state: boolean) => {
    setAddNew(state);
    props.loadQuestionTypes(setLoading);
  };

  const deleteQuestionTypeFn = async (questionType: questionType) => {
    if (questionType.id !== undefined) {
      setLoading(true);
      try {
        await axios.delete(`${API.QBank}/question-type/${questionType.id}`);
        props.loadQuestionTypes(setLoading);
        props.PushPopupAlert({
          title: `Question type removed successfully`,
          theme: PopupAlertTheme.success,
        });
        setSelectedQuestionType(null);
        // console.log("Deleted: ", res);
      } catch (error: any) {
        console.log("err: ", { ...error });
      }
      // setLoading(false);
    }
  };

  useEffect(() => {
    if (
      props.questionTypes.questionTypes === null ||
      props.questionTypes.questionTypes.length === 0
    ) {
      props.loadQuestionTypes(setLoading);
    }
  }, [props, setLoading]);
  return (
    <Fragment>
      <div className="mt-1">
        <div className="flex flex-row justify-between items-center bg-gray-100 py-1">
          <div className="text-blue-600 font-extrabold text-xl px-2">
            Lists of question types
          </div>
          {loading === false && (
            <div
              onClick={() => setAddNew(true)}
              className="btn bg-blue-600 text-white cursor-pointer hover:bg-blue-800"
            >
              Add new
            </div>
          )}
        </div>
        <div className="flex flex-col">
          {props.questionTypes.questionTypes === null || loading === true ? (
            <Loading />
          ) : props.questionTypes.questionTypes.length === 0 ? (
            <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
              <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
              <span>No question types available</span>
            </div>
          ) : (
            props.questionTypes.questionTypes.map((item, i) => (
              <div
                className={`border flex flex-row px-3 py-2 rounded items-center my-1 justify-between`}
              >
                <div className="flex flex-col">
                  <span className="text-gray-600 font-extrabold">
                    {item.title}
                  </span>
                  <span className={`text-xs pr-5 text-gray-500`}>
                    {item.description}
                  </span>
                </div>
                <div className="flex flex-row items-center gap-2">
                  <div
                    onClick={() =>
                      setSelectedQuestionType({
                        action: QT_QuestionTypeAction.View,
                        selectedQuestionType: item,
                      })
                    }
                    className="bg-gray-50 hover:bg-gray-200 rounded-full px-1 py-1 cursor-pointer"
                  >
                    <CgDetailsMore className="text-3xl text-gray-700" />
                  </div>
                  <div
                    onClick={() =>
                      setSelectedQuestionType({
                        action: QT_QuestionTypeAction.Update,
                        selectedQuestionType: item,
                      })
                    }
                    className="bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 cursor-pointer"
                  >
                    <MdEdit className="text-3xl text-blue-700" />
                  </div>
                  <div
                    onClick={() =>
                      setSelectedQuestionType({
                        action: QT_QuestionTypeAction.Delete,
                        selectedQuestionType: item,
                      })
                    }
                    className="bg-gray-100 hover:bg-red-100 rounded-full px-1 py-1 cursor-pointer"
                  >
                    <IoMdTrash className="text-3xl text-yellow-700" />
                  </div>
                </div>
              </div>
            ))
          )}
        </div>
      </div>
      {(addNew === true ||
        (selectedQuestionType !== null &&
          (selectedQuestionType.action === QT_QuestionTypeAction.Update ||
            selectedQuestionType.action === QT_QuestionTypeAction.View))) && (
        <Modal
          title={
            <div className="flex flex-row gap-4 items-center">
              <div className="flex flex-col">
                <span className="font-extrabold text-2xl text-gray-500">
                  {selectedQuestionType !== null &&
                  selectedQuestionType.action === QT_QuestionTypeAction.Update
                    ? selectedQuestionType.selectedQuestionType.title
                    : selectedQuestionType !== null &&
                      selectedQuestionType.action === QT_QuestionTypeAction.View
                    ? selectedQuestionType.selectedQuestionType.title
                    : "Add new question type"}
                </span>
                <span className="text-sm text-gray-500 font-normal">
                  {selectedQuestionType !== null &&
                  selectedQuestionType.action === QT_QuestionTypeAction.Update
                    ? "Update the selected question type"
                    : selectedQuestionType !== null &&
                      selectedQuestionType.action === QT_QuestionTypeAction.View
                    ? "View more details for the selected question type"
                    : "New question type will be visible at the time of question preparation"}
                </span>
              </div>
            </div>
          }
          backDrop={true}
          theme={Themes.default}
          close={() => {
            setAddNew(false);
            setSelectedQuestionType(null);
          }}
          widthSizeClass={ModalSize.medium}
          displayClose={true}
          backDropClose={true}
          padding={{ title: true, body: true }}
        >
          <AddQuestionType
            closeModal={closeAddNewModal}
            questionTypes={props.questionTypes}
            PushPopupAlert={props.PushPopupAlert}
            selectedQuestionType={selectedQuestionType}
            deleteSelected={setSelectedQuestionType}
          />
        </Modal>
      )}
      {selectedQuestionType !== null &&
        selectedQuestionType.action === QT_QuestionTypeAction.Delete && (
          <Modal
            title={
              <div className="flex flex-row gap-4 items-center">
                <div className="flex flex-col">
                  <span className="font-extrabold text-2xl text-gray-500">
                    {selectedQuestionType.selectedQuestionType.title}
                  </span>
                </div>
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() => setSelectedQuestionType(null)}
            widthSizeClass={ModalSize.medium}
            displayClose={true}
            backDropClose={true}
            padding={{ title: true, body: true }}
          >
            <div className="-mt-6">
              <span className="text-red-600 font-bold">
                Are you sure do you want to delete this question type?
              </span>
              <div className="flex flex-row items-center justify-between mt-2">
                <div
                  onClick={() => setSelectedQuestionType(null)}
                  className="btn bg-gray-100 text-gray-700 cursor-pointer hover:bg-gray-200"
                >
                  Cancel
                </div>
                <div
                  onClick={() =>
                    deleteQuestionTypeFn(
                      selectedQuestionType.selectedQuestionType
                    )
                  }
                  className="btn bg-red-600 text-white cursor-pointer hover:bg-red-800"
                >
                  Yes, Delete
                </div>
              </div>
            </div>
          </Modal>
        )}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  periods,
  schoolLevels,
  questionTypes,
  system,
}: StoreState): {
  auth: Auth;
  periods: applicationPeriodsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  questionTypes: questionTypesStoreInterface;
  system: System;
} => {
  return {
    auth: auth,
    periods: periods,
    schoolLevels: schoolLevels,
    questionTypes: questionTypes,
    system: system,
  };
};

const QuestionTypes = connect(mapStateToProps, {
  loadApplicationPeriods,
  setSelectedPeriod,
  loadEducationCategories,
  setSelectedEducationCategory,
  loadQuestionTypes,
  PushPopupAlert,
})(_QuestionTypes);

export default QuestionTypes;
