import React from 'react'
import { ImCheckboxChecked, ImCheckboxUnchecked } from 'react-icons/im'
import { schoolLevelsStoreInterface } from '../../../../actions/school_levels'

interface EducationCategorySelectProps {
  schoolLevels: schoolLevelsStoreInterface;
  education_categories: string[];
  setEducationCategories: (item: string[]) => void;
  loading: boolean;
}

const EducationCategorySelect = (props: EducationCategorySelectProps) => {
  return (
    <div className="rounded flex flex-col gap-2 overflow-y-auto border pb-3 px-3" style={{height: '80vh'}}>
      <div className="flex flex-row items-center gap-2">
        <span className="text-sm text-gray-600 font-bold mt-2 mb-1">Choose school level</span><span className="text-xs text-gray-500 italic mt-2 mb-1">(Optional)</span>
      </div>
      {props.schoolLevels.education_categories?.map((item, i) => (
        <div onClick={() => props.loading === false && props.setEducationCategories(props.education_categories.find(itm => itm === item.education_institute_id) ? props.education_categories.filter(x => x !== item.education_institute_id) :[...props.education_categories, item.education_institute_id])} key={i + 1}className={`flex flex-row items-center gap-3 p-3 rounded cursor-pointer ${props.education_categories.find(itm => itm === item.education_institute_id) ? "bg-blue-50 text-blue-600" : props.loading === true ? "bg-gray-100 text-gray-400" : "bg-gray-100 text-gray-600"} ${props.loading === true ? "hover:bg-gray-100 hover:text-gray-400" : "hover:bg-blue-100 hover:text-blue-600"}`}>
          {props.education_categories.find(itm => itm === item.education_institute_id) ?
          <ImCheckboxChecked className="text-3xl" /> :
          <ImCheckboxUnchecked className="text-3xl" />}
          <div className="flex flex-col gap-0">
            <span className="font-extrabold text-lg">{item.education_category_title}</span>
            <span className="text-xs -mt-1">{item.description}</span>
          </div>
        </div>
      ))}
    </div>
  )
}

export default EducationCategorySelect
