import axios from "axios";
import React, { useEffect, useState } from "react";
import { schoolLevelsStoreInterface } from "../../../../actions/school_levels";
import { PopupAlertData } from "../../../../components/Alert/Popup";
import { formErrorInterface } from "../../../../interfaces/form";
import { PopupAlertTheme } from "../../../../interfaces/popupAlert";
import { API } from "../../../../utils/api";
import EducationCategorySelect from "../SettingsComponents/EducationCategorySelect";
import { SchoolLevelsAction, S_SchoolLevelDetails } from "./SchoolLevels";

interface AddSchoolLevelProps {
  closeModal: (state: boolean) => void;
  school_levels: schoolLevelsStoreInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
  selectedSchoolLevel: S_SchoolLevelDetails | null;
  deleteSelected: (selectedDetail: S_SchoolLevelDetails) => void;
}

const AddSchoolLevel = (props: AddSchoolLevelProps) => {
  // States
  const [school_level_id, setSchool_level_id] = useState<string>("");
  const [school_level_title, setSchool_level_title] = useState<string>("");
  const [description, setDescription] = useState<string>("");
  const [education_category, setEducation_category] = useState<string[]>([]);
  const [formError, setFormError] = useState<formErrorInterface | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const submitSchoolLevel = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // validation
    if (
      props.selectedSchoolLevel?.action === SchoolLevelsAction.Update &&
      school_level_id === ""
    ) {
      return setFormError({
        element: "school_level_id",
        msg: "Please choose School level",
      });
    } else if (school_level_title === "") {
      return setFormError({
        element: "school_level_title",
        msg: "Please fill the school level name",
      });
    } else if (description === "") {
      return setFormError({
        element: "description",
        msg: "Please fill the description",
      });
    } else {
      setLoading(true);
      try {
        // Update current
        if (
          props.selectedSchoolLevel !== null &&
          props.selectedSchoolLevel.selectedSchoolLevel.school_level_id !==
            undefined
        ) {
          await axios.patch(`${API.APPLICATION}/schoollevel`, {
            school_level_id: school_level_id,
            school_level_name: school_level_title,
            description: description,
            education_institute: education_category,
            courses: [],
          });
          props.PushPopupAlert({
            title: `School level updated successfully`,
            description: `After updating the School level, you have access to delete and update it!`,
            theme: PopupAlertTheme.info,
          });
          props.closeModal(false);
          // console.log("Result: ", res);
        } else {
          // Add new
          await axios.post(`${API.APPLICATION}/schoollevel`, {
            school_level_name: school_level_title,
            description: description,
            education_institute: education_category,
            courses: [],
          });
          props.PushPopupAlert({
            title: `School level added successfully`,
            description: `After adding the School level, you have access to delete and update it!`,
            theme: PopupAlertTheme.success,
          });
          props.closeModal(false);
          // console.log("Result: ", res);
        }
      } catch (error: any) {
        props.PushPopupAlert({
          title: `Failed to add School level! Try again`,
          theme: PopupAlertTheme.danger,
        });
        console.log("Err: ", { ...error });
      }
      setLoading(false);
    }
  };

  useEffect(() => {
    if (props.selectedSchoolLevel !== null) {
      if (school_level_id === "") {
        setSchool_level_id(
          props.selectedSchoolLevel.selectedSchoolLevel.school_level_id
        );
      }
      if (school_level_title === "") {
        setSchool_level_title(
          props.selectedSchoolLevel.selectedSchoolLevel.school_level_name
        );
      }
      if (description === "") {
        setDescription(
          props.selectedSchoolLevel.selectedSchoolLevel.description
        );
      }
      if (education_category.length === 0) {
        setEducation_category(
          props.selectedSchoolLevel.selectedSchoolLevel.education_institute
        );
      }
    }
  }, [
    props,
    school_level_id,
    school_level_title,
    description,
    education_category,
  ]);
  return (
    <form onSubmit={submitSchoolLevel} className="-mt-2">
      <div className="grid md:grid-cols-12 gap-5">
        <div className="col-span-6">
          <EducationCategorySelect
            schoolLevels={props.school_levels}
            education_categories={education_category}
            setEducationCategories={setEducation_category}
            loading={loading}
          />
        </div>
        <div className="col-span-6">
          <div className="flex flex-col mb-2">
            <span className="text-sm text-gray-600 font-bold mb-1">Title</span>
            <input
              disabled={loading}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                setSchool_level_title(e.target.value);
                setFormError(null);
              }}
              type="text"
              value={school_level_title}
              className={`input-white border-2 ${
                formError !== null && formError.element === "school_level_title"
                  ? "border-red-500"
                  : "border-gray-500"
              }`}
            />
            {formError !== null &&
              formError.element === "school_level_title" && (
                <span className="text-red-600 font-bold text-xs">
                  {formError.msg}
                </span>
              )}
          </div>
          <div className="flex flex-col mb-2">
            <span className="text-sm text-gray-600 font-bold mt-2 mb-1">
              Description
            </span>
            <textarea
              disabled={loading}
              onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
                setDescription(e.target.value);
                setFormError(null);
              }}
              value={description}
              className={`input-white border-2 ${
                formError !== null && formError.element === "description"
                  ? "border-red-500"
                  : "border-gray-500"
              }`}
              style={{ minHeight: "100px" }}
            >
              {description}
            </textarea>
            {formError !== null && formError.element === "description" && (
              <span className="text-red-600 font-bold text-xs">
                {formError.msg}
              </span>
            )}
          </div>
          <div className="flex flex-row justify-between items-center">
            {props.selectedSchoolLevel?.action === SchoolLevelsAction.View ? (
              <button
                onClick={() =>
                  props.selectedSchoolLevel &&
                  props.deleteSelected({
                    action: SchoolLevelsAction.Delete,
                    selectedSchoolLevel:
                      props.selectedSchoolLevel.selectedSchoolLevel,
                  })
                }
                type="button"
                className="btn border border-red-600 text-red-700"
              >
                Delete
              </button>
            ) : (
              <div></div>
            )}
            <button
              disabled={loading}
              type="submit"
              className={`btn bg-blue-600 text-white ${
                loading === true
                  ? "animate__animated animate__flash animate__infinite"
                  : ""
              }`}
            >
              {loading === true
                ? "Please wait..."
                : props.selectedSchoolLevel !== null
                ? "Update"
                : "Add"}
            </button>
          </div>
        </div>
      </div>
    </form>
  );
};

export default AddSchoolLevel;
