import axios from "axios";
import React, { useEffect, useState } from "react";
import { questionTypesStoreInterface } from "../../../../actions/questionTypes";
import { PopupAlertData } from "../../../../components/Alert/Popup";
import { formErrorInterface } from "../../../../interfaces/form";
import { PopupAlertTheme } from "../../../../interfaces/popupAlert";
import { questionTypeElements } from "../../../../interfaces/question";
import { API } from "../../../../utils/api";
import { QT_QuestionTypeAction, QT_QuestionTypeDetails } from "./QuestionTypes";

interface AddQuestionTypeProps {
  closeModal: (state: boolean) => void;
  questionTypes: questionTypesStoreInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
  selectedQuestionType: QT_QuestionTypeDetails | null;
  deleteSelected: (selectedDetail: QT_QuestionTypeDetails) => void;
}

const AddQuestionType = (props: AddQuestionTypeProps) => {
  // States
  const [title, setTitle] = useState<string>("");
  const [element, setElement] = useState<string>("");
  const [description, setDescription] = useState<string>("");
  const [formError, setFormError] = useState<formErrorInterface | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const submitQuestionType = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // validation
    if (title === "") {
      return setFormError({
        element: "title",
        msg: "Please fill the title",
      });
    } else if (element === "" || element.length === 0) {
      return setFormError({
        element: "element",
        msg: "Please fill the element",
      });
    } else if (description === "") {
      return setFormError({
        element: "description",
        msg: "Please fill the description",
      });
    } else {
      setLoading(true);
      try {
        // Update current
        if (
          props.selectedQuestionType !== null &&
          props.selectedQuestionType.selectedQuestionType.id !== undefined
        ) {
          await axios.put(
            `${API.QBank}/question-type/${props.selectedQuestionType.selectedQuestionType.id}`,
            {
              status: 1,
              title: title,
              element: element,
              description: description,
            }
          );
          props.PushPopupAlert({
            title: `Question type updated successfully`,
            description: `After updating the question type, you have access to delete and update it!`,
            theme: PopupAlertTheme.info,
          });
          props.closeModal(false);
          // console.log("Result: ", res);
        } else {
          // Add new
          await axios.post(`${API.QBank}/question-type`, {
            status: 1,
            title: title,
            element: element,
            description: description,
          });
          props.PushPopupAlert({
            title: `Question type added successfully`,
            description: `After adding the question type, you have access to delete and update it!`,
            theme: PopupAlertTheme.success,
          });
          props.closeModal(false);
          // console.log("Result: ", res);
        }
      } catch (error: any) {
        props.PushPopupAlert({
          title: `Failed to add question type! Try again`,
          theme: PopupAlertTheme.danger,
        });
        console.log("Err: ", { ...error });
      }
      setLoading(false);
    }
  };

  useEffect(() => {
    if (props.selectedQuestionType !== null) {
      if (title === "") {
        setTitle(props.selectedQuestionType.selectedQuestionType.title);
      }
      if (element === "") {
        setElement(props.selectedQuestionType.selectedQuestionType.element);
      }
      if (description === "") {
        setDescription(
          props.selectedQuestionType.selectedQuestionType.description
        );
      }
    }
  }, [props, title, element, description]);
  return (
    <form onSubmit={submitQuestionType} className="-mt-2">
      <div className="flex flex-col mb-2">
        <span className="text-sm text-gray-600 font-bold mt-2 mb-1">
          Element
        </span>
        {props.selectedQuestionType !== null ? (
          <div className="bg-blue-100 text-blue-600 font-bold px-3 py-2 rounded">
            {
              questionTypeElements.find(
                (itm) =>
                  props.selectedQuestionType &&
                  itm.key ===
                    props.selectedQuestionType.selectedQuestionType.element
              )?.value
            }
          </div>
        ) : (
          <select
            disabled={loading}
            onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
              setElement(e.target.value);
              setFormError(null);
            }}
            className={`input-white border-2 ${
              formError !== null && formError.element === "element"
                ? "border-red-500"
                : "border-gray-500"
            }`}
          >
            <option value="">Choose element</option>
            {questionTypeElements.map((item, i) => (
              <>
                {props.questionTypes.questionTypes &&
                  props.questionTypes.questionTypes.find(
                    (itm) => itm.element === item.key
                  ) === undefined && (
                    <option
                      selected={element === item.key ? true : false}
                      key={i + 1}
                      value={item.key}
                    >
                      {item.value}
                    </option>
                  )}
              </>
            ))}
          </select>
        )}
        {formError !== null && formError.element === "element" && (
          <span className="text-red-600 font-bold text-xs">
            {formError.msg}
          </span>
        )}
      </div>
      <div className="flex flex-col mb-2">
        <span className="text-sm text-gray-600 font-bold mb-1">Title</span>
        <input
          disabled={loading}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            setTitle(e.target.value);
            setFormError(null);
          }}
          type="text"
          value={title}
          className={`input-white border-2 ${
            formError !== null && formError.element === "title"
              ? "border-red-500"
              : "border-gray-500"
          }`}
        />
        {formError !== null && formError.element === "title" && (
          <span className="text-red-600 font-bold text-xs">
            {formError.msg}
          </span>
        )}
      </div>
      <div className="flex flex-col mb-2">
        <span className="text-sm text-gray-600 font-bold mt-2 mb-1">
          Description
        </span>
        <textarea
          disabled={loading}
          onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
            setDescription(e.target.value);
            setFormError(null);
          }}
          value={description}
          className={`input-white border-2 ${
            formError !== null && formError.element === "description"
              ? "border-red-500"
              : "border-gray-500"
          }`}
          style={{ minHeight: "100px" }}
        >
          {description}
        </textarea>
        {formError !== null && formError.element === "description" && (
          <span className="text-red-600 font-bold text-xs">
            {formError.msg}
          </span>
        )}
      </div>
      <div className="flex flex-row justify-between items-center">
        {props.selectedQuestionType?.action === QT_QuestionTypeAction.View ? (
          <button
            onClick={() =>
              props.selectedQuestionType &&
              props.deleteSelected({
                action: QT_QuestionTypeAction.Delete,
                selectedQuestionType:
                  props.selectedQuestionType.selectedQuestionType,
              })
            }
            type="button"
            className="btn border border-red-600 text-red-700"
          >
            Delete
          </button>
        ) : (
          <div></div>
        )}
        <button
          disabled={loading}
          type="submit"
          className={`btn bg-blue-600 text-white ${
            loading === true
              ? "animate__animated animate__flash animate__infinite"
              : ""
          }`}
        >
          {loading === true
            ? "Please wait..."
            : props.selectedQuestionType !== null
            ? "Update question type"
            : "Add question type"}
        </button>
      </div>
    </form>
  );
};

export default AddQuestionType;
